  numberOnly(event: { which: any; keyCode: any; }): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57) || charCode == 109) {
      if (charCode==65 || charCode==97 || charCode==108 || charCode==76)
      {
        return true
      }
      else
      {
        return false;
      }
    }
    return true;
  }

  onFetechCustGroup() {
    this.mcCustomerGroup = '';
    this.mcSearchtext = '';
    if (this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == null) {
      this.templateError = "Please enter Cust Group"
    }
    else {
      this.templateError = '';
      let queryParams = new HttpParams();
      queryParams = queryParams.append('CCOMPGRP', this.pDARSPSLAForm.get("mcCustomerGroup")?.value);
      this.pDARSPSLAService.getCustGroupDetails(queryParams).subscribe((res: any) => {
        if (res.result) {

          if (res.result.custGroupList == null || res.result.custGroupList === 0 || res.result.custGroupList.length === 0) {
            if (this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "ALL" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "All" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "all" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "All" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "all" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "aLL" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "aLl")
            {
              this.mcSearchtext='ALL';
            }
            else{
            this.templateError = "No Data for" + "   " + "[" + this.pDARSPSLAForm.get("mcCustomerGroup")?.value + "]" + "   " + "this Cust Group";
            }
          }
          else {
            this.pDARSPSLAForm.patchValue({
              mcCustomerGroup: res.result.custGroupList[0].CCOMPGRP,
              mcSearchtext: res.result.custGroupList[0].CCOMPNAME
            });
          }
          if (this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "ALL" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "All" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "all" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "All" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "all" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "aLL" || this.pDARSPSLAForm.get("mcCustomerGroup")?.value == "aLl")
            {
              this.pDARSPSLAForm.patchValue({
              mcCustomerGroup: "ALL",
              mcSearchtext: "ALL"
            });
            }
        }
      })
    }
  }

  
