import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AppConstants } from 'src/app/shared/app.constants';

@Injectable({
  providedIn: 'root'
})
export class LiveTrackerStatusAndScanService {
  constructor(public httpClient: HttpClient) { }

  public getAllDetails(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.PCAL_DATA, { params: body });
  }
  public getAllPDSTDetails(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.PCAL_DATA, { params: body });
  }
  public getCustGroupDetails(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.CUSTGROUP_DATA, { params: body });
  }

  public getAllPTYPE2Details(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.PTYPE2Details, { params: body });
  }

  public getStatusDetails(body: any): Observable<any> {
    return this.httpClient.post<any>(AppConstants.StatusDetails,body);
  }

  public getAllPDST1Details(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.PDST1_DATA, { params: body });
  }

}
