import { Component } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { NotificationAlertService,AESEncryptDecryptService } from 'bd-common-lib';
import * as moment from 'moment';
import { Router, ActivatedRoute } from '@angular/router';
import { PDARSPSLAService } from 'src/app/services/PDARSPSLA/PDARSPSLA.service';
import { HttpParams } from '@angular/common/http';
import { GlobalVar } from 'src/app/shared/services/global-var';

@Component({
  selector: 'app-kam-customer-report',
  templateUrl: './kam-customer-report.component.html',
  styleUrls: ['./kam-customer-report.component.css']
})
export class KamCustomerReportComponent {
  KamCustomerReportForm!: FormGroup;
  KamCustomerReportData: any[] = [];
  templateError = '';
  PDSTList: any[] = [];
  PDST1List: any[] = [];
  calanderList: any[] = [];
  ShowStatus:boolean=false;
  hideURL:boolean=false;
  constructor(
    public pDARSPSLAService: PDARSPSLAService, 
    private fb: FormBuilder,
    public encrypt:AESEncryptDecryptService,
    public alert: NotificationAlertService) { }

  ngOnInit(): void {
    this.createForm();
      this.ShowStatus=false;
      this.getCalanderDetails();
  }
  
  getCalanderDetails() {
    {
      let queryParams = new HttpParams();    
      queryParams = queryParams.append('puid','');
      this.pDARSPSLAService.getAllCalanderDetails(queryParams).subscribe((res: any) => {
        if (res.result) {        
          this.calanderList = res.result.list;
        }
      })      
    }
  }

  resetDetails() {
    this.ShowStatus=false;
    this.createForm();
    this.KamCustomerReportData=[];
  }
  
  toggleSetCustomer(item: any) {
    console.log(item.target.value);
    if (item.target.value!="I")   
    {
      this.ShowStatus=true;
    }
    else
    {
      this.ShowStatus=false;
    }
  }

  ppnfs: boolean = false;
  ppnfsentry() {
    this.ppnfs = !this.ppnfs;
  }
  createForm() {
    this.KamCustomerReportForm = this.fb.group({  
      mcURL:[],    
      PPROD : ['I'],
      PTYPE : ['A'],
      PCMTH:['JUL2023']
    });
  }
  
  myReportsFun() {
    var PCMTH = this.KamCustomerReportForm.get('PCMTH')?.value == undefined ? null : this.KamCustomerReportForm.get('PCMTH')?.value == undefined ? null : this.KamCustomerReportForm.get('PCMTH')?.value;
    var PPROD = this.KamCustomerReportForm.get('PPROD')?.value == undefined ? null : this.KamCustomerReportForm.get('PPROD')?.value == undefined ? null : this.KamCustomerReportForm.get('PPROD')?.value;
    var PTYPE = this.KamCustomerReportForm.get('PTYPE')?.value == undefined ? null : this.KamCustomerReportForm.get('PTYPE')?.value == undefined ? null : this.KamCustomerReportForm.get('PTYPE')?.value;
    var reportCall = "";
     
    PPROD=this.KamCustomerReportForm.get('PPROD')?.value;
    PTYPE=this.KamCustomerReportForm.get('PTYPE')?.value;
    PCMTH=this.KamCustomerReportForm.get('PCMTH')?.value;
    
    var cnewrpturl = this.encrypt.decryptionAES(sessionStorage.getItem('reporturl'));
    if (PPROD=="I")
    {
    var ALLSTR = "&tmthyr="+PCMTH;
        reportCall = cnewrpturl +"/home/controltower/icicireport.rptdesign"+ALLSTR+"&userID=reportuser";			
    }
    else
    {
    var ALLSTR = "&tmthyr="+PCMTH+"&tstat="+PTYPE+"&treportflg="+PPROD;
        reportCall = cnewrpturl +"/home/controltower/KAMCUSTREPORTS.RPTDESIGN"+ALLSTR+"&userID=reportuser";
    }
    
    this.KamCustomerReportForm.patchValue({
      mcURL: reportCall
    });
    window.open(encodeURI(reportCall), '_blank');
  }
  
}
