  numberOnly(event: { which: any; keyCode: any; }): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57) || charCode == 109) {
      if (charCode==65 || charCode==97 || charCode==108 || charCode==76)
      {
        return true
      }
      else
      {
        return false;
      }
    }
    return true;
  }

  onFetechCustGroup() {
    this.mcCustomerGroup = '';
    this.mcSearchtext = '';
    if (this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == null) {
      this.templateError = "Please enter Cust Group"
    }
    else {
      this.templateError = '';
      let queryParams = new HttpParams();
      queryParams = queryParams.append('CCOMPGRP', this.vendorwisePickupsForm.get("mcCustomerGroup")?.value);
      this.vendorwisePickupsService.getCustGroupDetails(queryParams).subscribe((res: any) => {
        if (res.result) {

          if (res.result.custGroupList == null || res.result.custGroupList === 0 || res.result.custGroupList.length === 0) {
            if (this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "ALL" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "All" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "all" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "All" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "all" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "aLL" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "aLl")
            {
              this.mcSearchtext='ALL';
            }
            else{
            this.templateError = "No Data for" + "   " + "[" + this.vendorwisePickupsForm.get("mcCustomerGroup")?.value + "]" + "   " + "this Cust Group";
            }
          }
          else {
            this.vendorwisePickupsForm.patchValue({
              mcCustomerGroup: res.result.custGroupList[0].CCOMPGRP,
              mcSearchtext: res.result.custGroupList[0].CCOMPNAME
            });
          }
          if (this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "ALL" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "All" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "all" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "All" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "all" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "aLL" || this.vendorwisePickupsForm.get("mcCustomerGroup")?.value == "aLl")
            {
              this.vendorwisePickupsForm.patchValue({
              mcCustomerGroup: "ALL",
              mcSearchtext: "ALL"
            });
            }
        }
      })
    }
  }

   
