  toggleType(item: any) {
    console.log(item.target.value);
    //this.PTYPE2='';
    this.toggleSetProdCodeValue = item.target.value;
    if (item.target.value != null && (item.target.value == "A" || item.target.value == "H")) {
      if (item.target.value == null) {
        this.PTYPE2 = "ALL";
      }
      this.ShowBoth = true;
      this.ShowStatus = false;
      this.ShowScan = false;
      this.ShowPTYPE2 = true;
    }
    else {
      if (item.target.value != null && (item.target.value == "B" || item.target.value == "C" || item.target.value == "D" || item.target.value == "G")) {
        if (item.target.value == null) {
          this.PTYPE2 = "T";
        }
        this.ShowBoth = false;
        this.ShowStatus = true;
        this.ShowScan = false;
        this.ShowPTYPE2 = true;
      }
      else {
        if (item.target.value != null && (item.target.value == "F" || item.target.value == "E")) {
          if (item.target.value == null) {
            this.PTYPE2 = "S";
          }
          this.ShowBoth = false;
          this.ShowStatus = false;
          this.ShowScan = true;
          this.ShowPTYPE2 = true;
        }
      }
    }
   console.log("PTYPE2 value-",this.PTYPE2);
    if (item.target.value != null && (item.target.value == "D" || item.target.value == "G" || item.target.value == "E")) {
      this.ShowPTYPE2 = true;
    }
    let queryParams = new HttpParams();
    queryParams = queryParams.append('PPTYPE1', item.target.value);
    this.liveTrackerStatusAndScanService.getAllPTYPE2Details(queryParams).subscribe((res: any) => {
      if (res.result) {
        this.PDST2List = res.result.liveTrackStatusDetailsList;
        console.log("PTYPE2 value-",this.PDST2List);
      }
    })
    this.ShowPTYPE2 = true;
    console.log(this.PTYPE2);
  }

  toggleSetProdCode(item: any) {
    console.log(item.target.value);
    this.toggleSetProdCodeValue = item.target.value;
  }

  toggleDTYPE1Show(item: any) {
    console.log(item.target.value);
    if (item.target.value != null && !(item.target.value == "ALL" || item.target.value == "A" || item.target.value == "B")) {
      this.DTYPE1Show = true;
    }
    else {
      this.DTYPE1Show = false;
    }
  }

  toggleSetRegion(item: any) {
    this.toggleSetRegionValue = item.target.value;
    console.log(item.target.value);
    if (item.target.value == "ALL") {
      this.GeoShow = false;
      this.PDSTShow = false;
    }
    else {
      this.GeoShow = true;
      this.PDSTShow = false;
      this.liveTrackerStatusAndScanForm.patchValue({
        DREG: 'A'
      });
    }
    console.log("End--", this.GeoShow);
  }

  toggleGetPDSTDetails(item: any) {
    if (this.liveTrackerStatusAndScanForm.get('DREG')?.value == "A" || this.liveTrackerStatusAndScanForm.get('OREG')?.value == "ALL") {
      this.PDSTShow = false;
    }
    else {
      this.PDSTShow = true;
    }

    if (this.PDSTShow == true) {
      if (item.target.value == "B") {
        this.ForBrachShow = true;
        this.ForLocationShow = false;
        this.ForAreaShow = false;
        this.ForAltareaShow = false;
      }
      else if (item.target.value == "S") {
        this.ForBrachShow = false;
        this.ForLocationShow = true;
        this.ForAreaShow = false;
        this.ForAltareaShow = false;
      }
      else if (item.target.value == "E") {
        this.ForBrachShow = false;
        this.ForLocationShow = false;
        this.ForAreaShow = true;
        this.ForAltareaShow = false;
      }
      else if (item.target.value == "L") {
        this.ForBrachShow = false;
        this.ForLocationShow = false;
        this.ForAreaShow = false;
        this.ForAltareaShow = true;
      }
      else if (item.target.value == "A") {
        this.ForBrachShow = false;
        this.ForLocationShow = false;
        this.ForAreaShow = false;
        this.ForAltareaShow = false;
        this.ForAllShow = true;
      }

      this.toggleSetgeoDREGalue = item.target.value;
      let queryParams = new HttpParams();
      queryParams = queryParams.append('pctype', item.target.value);
      queryParams = queryParams.append('pcregion', this.toggleSetRegionValue);
      this.liveTrackerStatusAndScanService.getAllPDSTDetails(queryParams).subscribe((res: any) => {
        if (res.result) {
          this.PDSTList = res.result.geoDetailsList;
        }
        this.liveTrackerStatusAndScanForm.patchValue({
          PDST: 'XXX'
        });
      })
    }
  }

  fetchDetails() {
    if (this.liveTrackerStatusAndScanForm.value.mcProductType) {
      const payload = {
        pctype: 'A',
        pcregion: 'All'
      }

      let queryParams = new HttpParams();
      queryParams = queryParams.append('pctype', this.liveTrackerStatusAndScanForm.value.mcProductType ? 'A' : 'A');
      queryParams = queryParams.append("pcregion", this.liveTrackerStatusAndScanForm.value.mcRegion ? 'ALL' : 'ALL');
      this.liveTrackerStatusAndScanService.getAllDetails(queryParams).subscribe((res: any) => {
        if (res.result) {
          this.liveTrackerStatusAndScanData = res.result.geoDetailsList;
        }
      })
    }
  }

