  DownloadFile() {
    const payload = {
      "PPROD": 'A',
      "PCOMPGRP": 'ALL',
      "PNAME": 'ALL',
      "PREG": 'ALL',
      "RTYPE": 'A',
      "RVALUE": 'XXX',
      "LTYPE": 'A',
      "PLEG": 'BALL',
      "PTYPE1": 'F',
      "PDATE": 'ALLCO',
      "PDATE1": 'ALLCO',
      "PTYPE2": 'S010',
      "POUT": 'H1',
      "PCUSER": '114516Status%20updated%20',
      "PLOCTYPE": 'ALL',
      "PEDD": 'ALL',
      "PDTYPE1": 'ALL',
      "PSDATE": '',
      "PSDATE1": ''
    }
    this.liveTrackerStatusAndScanService.getStatusDetails(payload).subscribe((data: any) => {
      if (data.result.cur != null) {
      let responseData:any[]=[];
      this.preResponseData = data;
      if(this.preResponseData.status.isSuccess==true)
        {
           let CDESC = this.preResponseData.result.chdr;         
          console.log("chdr--",this.preResponseData.result.chdr);
          let selectedcurErr = this.preResponseData.result.cur;
          for(let i=0; i < selectedcurErr.length; i++)
          {
            const reorderedDatacurErr = {
              CDESC: selectedcurErr[i].CDESC
             }
             responseData.push(reorderedDatacurErr);
          }
          this.csvService.exportToCsv("FileDownload"+'.csv', responseData);
        }
      }
    });   
  } 

  numberOnly(event: { which: any; keyCode: any; }): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57) || charCode == 109) {
      if (charCode == 65 || charCode == 97 || charCode == 108 || charCode == 76) {
        return true
      }
      else {
        return false;
      }
    }
    return true;
  }

