  onFetechCustGroup() {
    this.mcCustomerGroup = '';
    this.mcSearchtext = '';
    if (this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == null) {
      this.templateError = "Please enter Cust Group"
    }
    else {
      this.templateError = '';
      let queryParams = new HttpParams();
      queryParams = queryParams.append('CCOMPGRP', this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value);
      this.liveTrackerStatusAndScanService.getCustGroupDetails(queryParams).subscribe((res: any) => {
        if (res.result) {

          if (res.result.custGroupList == null || res.result.custGroupList === 0 || res.result.custGroupList.length === 0) {
            if (this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "ALL" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "All" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "all" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "All" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "all" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "aLL" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "aLl") {
              this.mcSearchtext = 'ALL';
            }
            else {
              this.templateError = "No Data for" + "   " + "[" + this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value + "]" + "   " + "this Cust Group";
            }
          }
          else {
            this.liveTrackerStatusAndScanForm.patchValue({
              mcCustomerGroup: res.result.custGroupList[0].CCOMPGRP,
              mcSearchtext: res.result.custGroupList[0].CCOMPNAME
            });
          }
          if (this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "ALL" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "All" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "all" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "All" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "all" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "aLL" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "aLl") {
            this.liveTrackerStatusAndScanForm.patchValue({
              mcCustomerGroup: "ALL",
              mcSearchtext: "ALL"
            });
          }
        }
      })
    }
  }

  onCustGroupEnter(event: any) {
    this.mcCustomerGroup = '';
    this.mcSearchtext = '';
    if (this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == null) {
      this.templateError = "Please enter Cust Group"
    }
    else {
      if (event.keyCode == 13 || event.keyCode == 9) {
        this.templateError = '';
        let queryParams = new HttpParams();
        queryParams = queryParams.append('CCOMPGRP', this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value);
        this.liveTrackerStatusAndScanService.getCustGroupDetails(queryParams).subscribe((res: any) => {
          if (res.result) {

            if (res.result.custGroupList == null || res.result.custGroupList === 0 || res.result.custGroupList.length === 0) {
              if (this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "ALL" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "All" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "all" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "All" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "all" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "aLL" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "aLl") {
                this.mcSearchtext = 'ALL';
              }
              else {
                this.templateError = "No Data for" + "   " + "[" + this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value + "]" + "   " + "this Cust Group";
                this.liveTrackerStatusAndScanForm.patchValue({
                  mcSearchtext: ""
                });
              }
            }
            else {
              this.liveTrackerStatusAndScanForm.patchValue({
                mcCustomerGroup: res.result.custGroupList[0].CCOMPGRP,
                mcSearchtext: res.result.custGroupList[0].CCOMPNAME
              });
            }
            if (this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "ALL" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "All" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "all" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "All" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "all" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "aLL" || this.liveTrackerStatusAndScanForm.get("mcCustomerGroup")?.value == "aLl") {
              this.liveTrackerStatusAndScanForm.patchValue({
                mcCustomerGroup: "ALL",
                mcSearchtext: "ALL"
              });
            }
          }
        })
      }
    }
  }

