  numberOnly(event: { which: any; keyCode: any; }): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57) || charCode == 109) {
      if (charCode==65 || charCode==97 || charCode==108 || charCode==76)
      {
        return true
      }
      else
      {
        return false;
      }
    }
    return true;
  }

  onFetechCustGroup() {
    this.mcCustomerGroup = '';
    this.mcSearchtext = '';
    if (this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == null) {
      this.templateError = "Please enter Cust Group"
    }
    else {
      this.templateError = '';
      let queryParams = new HttpParams();
      queryParams = queryParams.append('CCOMPGRP', this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value);
      this.eddTrackerDatewiseService.getCustGroupDetails(queryParams).subscribe((res: any) => {
        if (res.result) {

          if (res.result.custGroupList == null || res.result.custGroupList === 0 || res.result.custGroupList.length === 0) {
            if (this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "ALL" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "All" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "all" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "All" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "all" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "aLL" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "aLl")
            {
              this.mcSearchtext='ALL';
            }
            else{
            this.templateError = "No Data for" + "   " + "[" + this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value + "]" + "   " + "this Cust Group";
            }
          }
          else {
            this.eddTrackerDatewiseForm.patchValue({
              mcCustomerGroup: res.result.custGroupList[0].CCOMPGRP,
              mcSearchtext: res.result.custGroupList[0].CCOMPNAME
            });
          }
          if (this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "ALL" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "All" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "all" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "All" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "all" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "aLL" || this.eddTrackerDatewiseForm.get("mcCustomerGroup")?.value == "aLl")
            {
              this.eddTrackerDatewiseForm.patchValue({
              mcCustomerGroup: "ALL",
              mcSearchtext: "ALL"
            });
            }
        }
      })
    }
  }

   
